// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import App from './App';
Vue.config.productionTip = false;

// import router from './router';
// ------------- start --------------------------------------------------------
//  vue 
import VueRouter from 'vue-router';
import VueResource from 'vue-resource';
//  | 
Vue.use(VueRouter);
Vue.use(VueResource);
// 
import './assets/fonts/iconfont.css';
// 
import goods from './components/body/goods/goods';
import ratings from './components/body/ratings/ratings';
import seller from './components/body/seller/seller';
// 
const routes = [
  { path: '/', redirect: '/goods' }, // url  '/goods'
  { path: '/goods', component: goods },
  { path: '/ratings', component: ratings },
  { path: '/seller', component: seller }
];
/* eslint-disable no-new */
// 
const router = new VueRouter({
  //  
  routes,
  //  router ,css item_activate
  linkActiveClass: 'item_active'
});

new Vue({
  el: '#app',
  data: {
    //  |  | ,
    eventHub: new Vue()
  },
  router,
  template: '<App/>',
  components: { App }
});



