<template>
  <div class="cart_control">
    <transition name='move'>
      <div class="cart-decrease icon icon-remove_circle_outline" v-show="food.count" @click="decrease_Cart($event)"></div>
    </transition>
    <div class="cart-count" v-show="food.count">{{ food.count }}</div>
    <div class='cart-add icon icon-add_circle' @click.stop="add_Cart($event)"></div>   <!-- $event DOM -->
  </div>
</template>

<script>
export default {
  props: {
    food: {
      type: Object
    }
  },
  methods: {
    add_Cart(event) {
      if (!event._constructed) {
        return;
      }
      //  key undefined |  false
      if (!this.food.count) {
        //  vue     |  Vue.set API
        this.$set(this.food, 'count', 1);   //  1
      } else {
        // ,  1
        this.$set(this.food, 'count', this.food.count + 1);
      }
      //   eventHub  | 
      this.$root.eventHub.$emit('cart_add', event.target);   //  event.target  
    },
    decrease_Cart(event) {
      if (!event._constructed) {
        return;
      }
      if (this.food.count) {
        //  vue     |  Vue.set API
        this.$set(this.food, 'count', this.food.count - 1);
      }
    }
  }
};
</script>

<style type='text/css' lang="less" scoped>
.cart_control {
  font-size: 0;
  .cart-decrease {
    display: inline-block;
    font-size: 24px;
    line-height: 1;   /*  */
    padding: 6px;
    color: #009688;
    &.move-enter, &.move-leave-active {
      opacity: 0;
      transform: translate3d(24px, 0, 0) rotate(180deg);
      transition: all .4s linear;   /*  0.4s, */
    }
    &.move-enter-active, &.move-leave {
      opacity: 1;
      transfrom: translate3d(0, 0, 0) rotate(180deg);
      transition: all .4s linear;   /* all  transition  */
    }
  }
  .cart-count {
    display: inline-block;
    font-size: 12px;
    width: 12px;
    text-align: center;
    color: black;
    vertical-align: top;
    margin-top: 12px;
  }
  .cart-add {
    display: inline-block;
    font-size: 24px;
    padding: 6px;
    color: #009688;
    line-height: 1;
  }
}
</style>



