<template>
  <div id="app">
    <v-header :seller="seller"></v-header>

    <div class="tab">
      <div class="tab-item">
        <!--  <a> -->
        <router-link to="/goods"></router-link>
      </div>
      <div class="tab-item">
        <router-link to="/ratings"></router-link>
      </div>
      <div class="tab-item">
        <router-link to="/seller"></router-link>
      </div>
    </div>
    <!-- keep-alive  ,, -->
    <keep-alive>
      <router-view :seller="seller"></router-view>
    </keep-alive>
  </div>
</template>

<script>
import header from './components/header/header.vue';

// 
const ERR_OK = 0;
//  
export default {
  data() {
    return {
      //  seller 
      seller: {}
    };
  },
  //  vue  ()
  created() {
    // then  | vue-resource 
    this.$http.get('/api/seller').then(response => {
      // let 
      // object
      let json = response.body;
      console.log(json.data);
      if (json.errno === ERR_OK) {
        this.seller = json.data;
      }
    }, response => {
      // 
    });
  },
  components: {
    'v-header': header
  }
};
</script>

<style type="text/css" lang="less">
.tab {
  display: flex;
  width: 100%;
  line-height: 40px;
  height: 40px;
  border-bottom: 1px solid rgba(7, 17, 27, .1);
  .tab-item {
    flex: 1;    /* flex-grow 1  */
    text-align: center;  /*  */
    a {
      display: block;
      font-size: 14px;
      color: rgb(77, 85, 93);
    }
    .item_active {
      color: #009688;
    }
  }
}

</style>



