<template>
  <div class="shopcart">
    <div class="content">
      <!-- .content  1 -->
      <div class="left" @click="toggle_List">
        <!-- .left  1 -->
        <div class="logo-wrapper" :class="{ active: total_Count > 0 }">
          <div class="logo">
            <span class="icon icon-shopping_cart"></span>
          </div>
          <div class="num">{{ total_Count }}</div>
        </div>
        <!-- .left  2 -->
        <div class="price" :class="{ active: total_Price > 0 }">{{ total_Price }}</div>
        <!-- .left  3 -->
        <div class="desc"> {{ deliveryPrice }}</div>
        <!-- .left  4 -->
        <div class='ball-wrapper'>
          <transition-group name="drop" v-on:before-enter="before_Drop" v-on:enter="droping" v-on:after-enter="after_Drop">
            <div v-for="(ball, index) of balls" v-if="ball.show" class='ball' :key="index">
              <div class="inner"></div>
            </div>
          </transition-group>
        </div>
      </div>
      <!-- .content  2 -->
      <div class="right" :class="{ enough: total_Price >= minPrice }" @click="pay">
        {{ pay_Desc }}
      </div>
    </div>

    <transition name="fold">
      <div class="shopcart-list" v-show="list_show">
        <div class="title">
          <h1></h1>
          <span @click="clear_Cart"></span>
        </div>
        <div ref="list_Content">
          <ul class="list">
            <li class='item' v-for="food of selectFoods">
              <div class="name">{{ food.name }}</div>
              <div class="price-wrapper">
                <span class="small"></span>
                <span class="price">{{ food.price * food.count }}</span>
              </div>
              <div class="cartcontrol-wrapper">
                <cart-control :food="food"></cart-control>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </transition>

    <transition name="fade">
      <div class="list-mask" v-show="!fold" @click="fold=!fold"></div>
    </transition>
  </div>
</template>

<script>
import BScroll from 'better-scroll';
// 
import cartControl from '../../../public/cart_control/cart_control.vue';
export default {
  data() {
    return {
      balls: [
        { show: false },
        { show: false },
        { show: false },
        { show: false },
        { show: false }
      ],
      dropBalls: [],  // 
      fold: true
    };
  },
  // 
  props: {
    selectFoods: {
      type: Array,
      // 
      default: function() {
        return [];
      }
    },
    deliveryPrice: {
      // 
      type: Number,
      // 
      default: 0
    },
    minPrice: {
      type: Number,
      default: 0
    }
  },
  // 
  computed: {
    total_Price() {
      let total = 0;
      // forEach | 
      this.selectFoods.forEach(food => {
        total += food.price * food.count;
      });
      return total;
    },
    total_Count() {
      let count = 0;
      this.selectFoods.forEach(food => {
        count += food.count;
      });
      return count;
    },
    pay_Desc() {
      // 
      let total_price = this.total_Price;
      // 
      let min_price = this.minPrice;
      if (total_price === 0) {
        // 
        return `${ min_price } `;
      } else if (total_price < min_price) {
        // 
        let diff = min_price - total_price;
        return `${ diff } `;
      } else {
        return ``;
      }
    },
    list_show() {
      if (!this.total_Count) {
        this.fold = true;
        return false;
      }
      let show = !this.fold;

      //  if  code  | 
      if (show) {
        this.$nextTick(() => {
          if (!this.scroll) {
            this.scroll = new BScroll(this.$refs.list_Content, {
              click: true
            });
          } else {
            this.scroll.refresh();
          }
        });
      }

      return show;
    }
  },
  // 
  created() {
    //   |
    this.$root.eventHub.$on('cart_add', this.drop);
  },
  methods: {
    //  | 
    drop(el) {
      // console.log(el);
      //  balls 
      for (let i = 0; i < this.balls.length; i++) {
        //  ball 
        let ball = this.balls[i];
        if (!ball.show) {
          ball.show = true;
          ball.el = el;
          this.dropBalls.push(ball);
          return;   // 
        }
      }
    },
    // 
    // vue 
    //  
    before_Drop(el) {
      for (let i = 0; i < this.balls.length; i++) {
        let ball = this.balls[i];
        if (ball.show) {
          //  dom   | 
          let rect = ball.el.getBoundingClientRect();
          // 32   
          let x = rect.left - 32;
          // 22   
          let y = -(window.innerHeight - rect.top - 22);
          el.style.display = '';
          el.style.transform = `translate3d(0, ${ y }px, 0)`;
          // 
          let inner = el.getElementsByClassName('inner')[0];
          inner.style.transform = `translate3d(${ x }px, 0, 0)`;
        }
      }
    },
    //  
    droping(el, done) {
      /* eslint-disable no-unused-vars */
      // 
      let ref = el.offsetHeight;
      this.$nextTick(() => {
        // DOM
        el.style.display = '';
        el.style.transform = 'translate3D(0, 0, 0)';
        let inner = el.getElementsByClassName('inner')[0];
        inner.style.transform = 'translate3D(0, 0, 0)';
        //  
        el.addEventListener('transitionend', done);
      });
    },
    //  
    after_Drop(el) {
      // shift()  
      let ball = this.dropBalls.shift();
      if (ball) {
        ball.show = false;
        el.style.display = 'none';
      }
    },
    toggle_List() {
      if (!this.total_Count) {
        return;
      }
      this.fold = !this.fold;
    },
    clear_Cart() {
      this.selectFoods.forEach(food => {
        food.count = 0;
      });
    },
    pay() {
      if (this.total_Price > this.minPrice) {
        alert(`${ this.total_Price }!`);
      }
    }
  },
  // 
  components: {
    cartControl
  }
};
</script>

<style type="text/css" lang="less" scoped>
.shopcart {
  position: fixed;   /*  */
  left: 0;
  bottom: 0;
  width: 100%;
  height: 48px;
  z-index: 5;
  .content {
    display: flex;   /*  */
    .left {
      flex: 1;
      background: #141d27;
      font-size: 0;
      white-space: nowrap;   /*  */
      /* .left  1 */
      .logo-wrapper {
        display: inline-block;
        position: relative;   /*  */
        top: -10px;
        width: 56px;
        height: 56px;
        margin-left: 12px;
        padding: 6px;
        box-sizing: border-box;
        border-radius: 50%;
        background: #141d27;
        .logo {
          width: 100%;
          height: 100%;
          border-radius: 50%;
          background: #2b343c;
          text-align: center;
          .icon {
            line-height: 44px;   /*    */
            font-size: 24px;
            color: rgba(255, 255, 255, .4);
          }
        }
        .num {
          position: absolute;   /*  */
          top: 0;
          right: 0;
          display: none;   /*  */
          font-size: 12px;
          font-weight: 700;
          width: 24px;
          height: 16px;
          line-height: 16px;
          text-align: center;
          color: #fff;
          background: red;
          border-radius: 16px;   /*  */
          box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 1);   /*  */
        }
        &.active {
          .logo {
            background: #009688;
            .icon {
              color: #fff;
            }
          }
          .num {
            display: block;
          }
        }
      }
      /* .left  2 */
      .price {
        display: inline-block;
        font-size: 16px;
        font-weight: 700;
        color: rgba(255, 255, 255, .4);
        border-right: 1px solid rgba(255, 255, 255, .1);
        line-height: 24px;
        margin-top: 12px;
        vertical-align: top;   /*  */
        padding-right: 6px;
        &.active {
          color: #fff;
        }
      }
      /* .left  3 */
      .desc {
        display: inline-block;
        font-size: 16px;
        font-weight: 200;
        color: rgba(255, 255, 255, .4);
        line-height: 24px;
        vertical-align: top;
        margin-top: 12px;
        padding-left: 6px;
      }
      .ball-wrapper {
        position: absolute;
        left: 32px;
        bottom: 20px;
        width: 16px;
        height: 16px;
        .ball {
          position: fixed;
          left: 32px;
          bottom: 22px;
          z-index: 8;
          transition: all .5s cubic-bezier(.37, -0.39, .63, -0.05);
          .inner {
            width: 16px;
            height: 16px;
            border-radius: 50%;
            transition: all .5s linear;
            background: #009688;
          }
        }
      }
    }
    .right {
      flex: 0 0 105px;   /*  */
      margin-left: -1px;
      line-height: 48px;
      background: #282E35;
      color: rgba(255, 255, 255, .4);
      font-size: 12px;
      font-weight: 700;
      text-align: center;
      &.enough {
        background: #009688;
        color: #fff;
        font-size: 14px;
      }
    }
  }
  .shopcart-list {
    position: absolute;
    left: 0;
    bottom: 48px;
    width: 100%;
    z-index: -1;
    &.fold-enter, &.fold-leave-active {
      opacity: 0;
      transform: translate3d(0, 100%, 0);   /* 100%   */
      transition: all .5s ease;   /*  */
    }
    &.fold-enter-active, &.fold-leave {
      opacity: 1;
      /*transform: translate3d(0, 0, 0);*/
      transition: all .5s ease;   /*  */
    }
    .title {
      height: 40px;
      line-height: 40px;
      padding: 0 18px;
      background: #f3f5f7;
      border-bottom: 1px solid rgba(7, 17, 27, .1);
      > h1 {
        float: left;
        font-size: 14px;
        font-weight: 700;
        color: rgb(7, 17, 27);
      }
      > span {
        float: right;
        font-size: 12px;
        color: #009688;
      }
    }
    .list {
      max-height: 217px;
      padding: 0 18px;
      overflow: hidden;
      /*overflow-y: scroll;*/
      background: #fff;
      .item {
        position: relative;
        padding: 12px 0;
        border-bottom: 1px solid rgba(7, 17, 27, .1);
        .name {
          font-size: 14px;
          color: rgb(7, 17, 27);
          line-height: 24px;
          font-weight: 700;
        }
        .price-wrapper {
          position: absolute;
          bottom: 12px;
          right: 90px;
          color: rgb(240, 20, 20);
          line-height: 24px;
          .small, .price {
            font-size: 14px;
            font-weight: 700;
          }
        }
        .cartcontrol-wrapper {
          position: absolute;
          right: 0;
          bottom: 6px;
        }
      }
    }
  }
  .list-mask {
    position: fixed;
    top: 0;
    left: 0;
    z-index: -2;
    width: 100%;
    height: 100%;
    background-color: rgba(7, 17, 27, .6);
    opacity: 1;
    &.fade-enter, &.fade-leave-active {
      opacity: 0;
      background-color: rgba(7, 17, 27, 0);
    }
    &.fade-enter-active, &.fade-leave {
      opacity: 1;
      transition: all .4s ease;
    }
  }
}
</style>



