<template>
  <div class="goods">
    <!-- .goods  1 -->     <!--   -->
    <div class="menu-wrapper" ref="menuWrapper">   <!-- ref vue  Dom | vue  ref . -->
      <ul>
        <li class="menu-item" v-for="(item, index) in goods"
        :class="{ active: currentIndex === index}" @click="select_Menu(index, $event)">   <!-- $event: DOM  -->
          <span class="title">
            <icon-type v-show="item.type > 0" :size="12" :index="item.type"></icon-type>
            <span class="text">
              {{ item.name }}
            </span>
          </span>
        </li>
      </ul>
    </div>
    <!-- .goods  2 -->      <!--    -->
    <div class="goods-wrapper" ref="goodsWrapper">   <!-- ref vue  Dom | vue  ref .-->
      <ul>
        <!--  v-for  DOM   -->
        <li class="food-list" v-for="item of goods" ref="goodsList">
          <h1 class="title">{{ item.name }}</h1>
          <ul>
            <li class="food" v-for="food of item.foods" @click="show_Food(food, $event)">
              <div class="img">
                <img :src="food.icon" alt="food.name"/>
              </div>
              <div class="content">
                <h1 class='name'>{{ food.name }}</h1>
                <p class="desc">{{ food.description }}</p>
                <div class="count-wrapper">
                  <span class="mon"> {{ food.sellCount }}</span>
                  <span> {{ food.rating }}%</span>
                </div>
                <div class="price-wrapper">
                  <span class="now_price"><small></small>{{ food.price }}</span>
                  <span class="old_price" v-show="food.oldPrice"><small></small>{{ food.oldPrice }}</span>
                </div>
                <div class="cart_control-wrapper">
                  <cart-control :food="food"></cart-control>
                </div>
              </div>
            </li>
          </ul>
        </li>
      </ul>
    </div>
    <shop-cart :select-foods="selectFoods" :delivery-price="seller.deliveryPrice" :min-price="seller.minPrice"></shop-cart>
    <food :food="selectedFood" ref="food"></food>
  </div>
</template>

<script>
//  
import BScroll from 'better-scroll';
// 
import iconType from '../../public/iconType/iconType.vue';
import cartControl from '../../public/cart_control/cart_control.vue';
import shopCart from './shopcart/shopcart.vue';
import food from './food/food.vue';

const ERR_OK = 0;

export default {
  data() {
    return {
      goods: [],
      listHeight: [],
      scrollY: 0,
      selectedFood: {}
    };
  },
  // 
  props: {
    //  Object
    seller: Object
  },
  // 
  computed: {
    currentIndex() {
      for (let i = 0; i < this.listHeight.length; i++) {
        //  
        let height_1 = this.listHeight[i];
        //  undefinde |  false
        let height_2 = this.listHeight[i + 1];
        //     Y 
        if (!height_2 || this.scrollY >= height_1 && this.scrollY < height_2) {
          return i;
        }
      }
      return 0;
    },
    selectFoods() {
      let foods = [];
      //   
      this.goods.forEach(good => {
        //   
        good.foods.forEach(food => {
          if (food.count) {
            foods.push(food);
          }
        });
      });
      return foods;
    }
  },
  //  vue 
  created() {
    // Vue $http  then | vue-resource 
    this.$http.get('./api/goods').then(res => {
      // let 
      // object
      let json = res.body;
      // console.log(json.data);
      if (json.errno === ERR_OK) {
        //  
        this.goods = json.data;
        //  DOM  js  Vue.nextTick() 
        this.$nextTick(() => {   //   DOM
        //    
          this._initScroll();
          this._countHeight();
        });
      }
    }, res => {
      // 
    });
  },
  methods: {
    _initScroll() {
      //  Dom 
      this.menuScroll = new BScroll(this.$refs.menuWrapper, {
        click: true   // 
      });
      this.goodsScroll = new BScroll(this.$refs.goodsWrapper, {
        click: true,
        probeType: 3   //  3: scroll   
      });
      //  | 
      this.goodsScroll.on('scroll', (pos) => {
        //   y | 
        this.scrollY = Math.abs(Math.round(pos.y));
        // this.scrollY = pos.y;
        // console.log('y' + this.scrollY);
      });
    },
    //  
    _countHeight() {
      //    ref="goodsList"  Dom  | 
      let goodsList = this.$refs.goodsList;
      let height = 0;
      // 
      this.listHeight.push(height);
      for (let i = 0; i < goodsList.length; i++) {
        //  dom 
        let item = goodsList[i];
        //   
        height += item.clientHeight;
        this.listHeight.push(height);
      }
    },
    //  
    select_Menu(index, event) {
      // js   _constructed 
      if (!event._constructed) {
        return;   // 
      }
      let goodsList = this.$refs.goodsList;
      let el = goodsList[index];
      //   1s
      this.goodsScroll.scrollToElement(el, 1000);
    },
    show_Food(food, event) {
      if (!event._constructed) {
        return;
      }
      this.selectedFood = food;
      //    
      this.$refs.food.show();
    }
  },
  // 
  components: {
    iconType,
    shopCart,
    cartControl,
    food
  }
};
</script>

<style type="text/css" lang="less" scoped>
.goods {
  display: flex;   /*  */
  position: absolute;   /*  */
  top: 175px;
  bottom: 46px;  /*  */
  width: 100%;
  overflow-y: hidden;   /*  -  */
  /* .goods  1 */
  .menu-wrapper {
    /*flex: 0 0 80px;*/
    flex-basis: 80px;   /*  | ()80px */
    background: #f3f5f7;
    .menu-item {
      display: table;
      height: 54px;
      width: 56px;
      line-height: 14px;   /*  */
      padding: 0 12px;
      .title {
        border-bottom: 1px solid rgba(7, 17, 27, .1);
        display: table-cell;
        width: 56px;
        font-size: 0;
        vertical-align: middle;
        .text {
          font-size: 12px;
          color: #009688;
        }
      }
      &.active {
        position: relative;
					margin-top: -1px;
					z-index: 10;
					font-weight: 700;
					background-color: #fff;
          .title {
            border-bottom: none;
          }
      }
    }
  }
  /* .goods  2 */
  .goods-wrapper {
    flex: 1;   /*  */
    .food-list {
      .title {
        font-size: 12px;
        color: rgb(147, 153, 159);
        height: 26px;
        line-height: 26px;
        border-left: 2px solid #d9dde1;
        background: #f3f5f7;
      }
      .food {
        display: flex;
        margin: 0 18px;
        padding: 18px 0;
        position: relative;
        &:not(:last-child):after {   /*    | */
          content: "";
          position: absolute;
          left: 0;
          bottom: 0;
          width: 100%;
          height: 1px;
          background: rgba(7, 17, 27, .1);
        }
        .img {
          flex: 0 0 50px;
          img {
            width: 100%;
            border-radius: 20%;
          }
        }
        .content {
          flex: 1;   /*  */
          margin-left: 10px;
          .name {
            font-size: 14px;
            /*font-weight: bold;*/
            color: rgb(7, 17, 27);
            line-height: 1;   /*  */
            margin-top: 2px;
          }
          .desc {
            font-size: 10px;
            color: rgb(147, 153, 159);
            line-height: 1;
            margin-top: 8px;
          }
          .count-wrapper {
            font-size: 10px;
            color: rgb(147, 153, 159);
            line-height: 1;
            margin-top: 8px;
            .mon {
              margin-right: 12px;
            }
          }
          .price-wrapper {
            margin-top: 8px;
            .now_price {
              font-size: 16px;
              line-height: 1;
              color: rgb(240, 20, 20);
              small {
                font-size: 5px;
              }
            }
            .old_price {
              font-size: 10px;
              color: rgb(147, 153, 159);
              line-height: 1;
              font-weight: 700;
              text-decoration: line-through;   /*  */
            }
          }
          .cart_control-wrapper {
            position: absolute;
            right: 0;
            bottom: 0;
          }
        }
      }
    }
  }
}
</style>



