	<div class="header">
    <!-- .header  1 -->
		<div class="content-wrapper">
      <!-- .content-wrapper  1 -->
			<div class="head_img">
				<img :src="seller.avatar" alt="" style="width: 64px; height: 64px;" />
			</div>
      <!-- .content-wrapper  2 -->
			<div class="content">
        <!-- .content  1 -->
				<div class="title">
					<span class="brand"></span>
					<span class="name">{{seller.name}}</span>
				</div>
        <!-- .content  2 -->
				<div class="desc">
					{{ seller.description }}/{{ seller.deliveryTime }}
				</div>
        <!-- .content  3 -->
        <!--  seller  |  true  -->
				<div v-if="seller.supports" class="supports">
					<span class="icon" :class="classMap[seller.supports[1].type]"></span>
					<span class="text">{{ seller.supports[1].description }}</span>
				</div>
			</div>
		</div>
    <!-- .header  2 -->
    <div v-if="seller.supports" class="supportsbtn" @click="show_Detail">
      <span class="count">{{ seller.supports.length }}</span>
      <i class="icon icon-keyboard_arrow_right"></i>
    </div>
    <!-- .header  3 -->
		<div class="bulletin-wrapper" @click="show_Detail">
			<span class="title"></span>
			<div class=" text out_text_hidden">{{ seller.bulletin }}</div>
			<i class="icon icon-keyboard_arrow_right"></i>
		</div>
		<!-- .header  4 -->
		<!--  -->
    <div class="bg_img">
      <img :src="seller.avatar" alt="" style="width: 100%; height: 100%;" />
    </div>
		<!-- .header  5 -->
		<!--  -->
		<transition name="fade">
			<div v-show="detail_Show" class="detail">
				<!-- .detail  1 -->
				<div class="content">
					<h1 class="name">{{ seller.name }}</h1>
					<!--  |  -->
					<star :size="20" :score="seller.score"></star>
					<div class="title">
						<div class="line"></div>
						<div class="text"></div>
						<div class="line"></div>
					</div>
					<ul v-if="seller.supports" class="supports">
						<li class="support-item" v-for="item in seller.supports">
							<icon-type :size="16" :index="item.type"></icon-type>
							<span class="text">{{ item.description }}</span>
						</li>
					</ul>
					<div class="title">
						<div class="line"></div>
						<div class="text"></div>
						<div class="line"></div>
					</div>

					<div class="bulletin">
						<p>{{ seller.bulletin }}</p>
					</div>
				</div>
				<!-- .detail  2 -->
				<!--  -->
				<div class="close">
					<span class="icon icon-close" @click="close_Detail"></span>
				</div>
			</div>
		</transition>
	</div>
</template>

<script>
// 
import star from '../public/star/star.vue';
import iconType from '../public/iconType/iconType.vue';

export default {
	data() {
		return {
			// 
			classMap: [],
			detail_Show: false
		};
	},
  props: {
    //  |
    seller: {
      //  Object
      type: Object
    }
  },
  //  vue  ()
  created: function() {
    this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
  },
	methods: {
		show_Detail() {
			this.detail_Show = true;
		},
		close_Detail() {
			this.detail_Show = false;
		}
	},
	// 
	components: {
		star,
		iconType
	}
};
</script>

<!-- scoped  -->
<style type="text/css" lang="less" scoped>
	.header {
    position: relative;   /*  */
		background: rgba(7, 17, 27, .5);
		color: #fff;
      /*.header  1 */
    .content-wrapper {
      padding: 24px 0 18px 24px;
      display: flex;
      /* .content-wrapper  1 */
      .head_img {
        display: inline-block;   /*  |  */
        width: 64px;
        height: 64px;
        margin-right: 16px;
        img {
          border-radius: 50%;   /*  */
					border: 2px solid #009688;
        }
      }
      /* .content-wrapper  2 */
      .content {
        display: flex;   /*  */
        flex-direction: column;   /*  */
        justify-content: space-between;   /*  */
        height: 64px;
        /* .content  1 */
        .title {
          height: 18px;
          line-height: 18px;
          font-size: 0;
          font-weight: bold;
          .brand {
            display: inline-block;
            width: 30px;
            height: 18px;
            margin-right: 6px;
            background: url('brand@2x.png') no-repeat;   /*    */
            background-size: 100% 100%;   /*  |  */
          }
          .name {
            vertical-align: top;   /*  */
            font-size: 16px;
          }
        }
        /* .content-wrapper  2 */
        .desc {
          height: 12px;
          font-size: 12px;
          font-weight: 200;   /*  */
          line-height: 12px;
        }
        /* .content-wrapper  3 */
        .supports {
          height: 10px;
          font-size: 10px;
          font-weight: 200;
          line-height: 10px;
          font-size: 0;
          .icon {
            display: inline-block;
            width: 12px;
            height: 12px;
            background-size: 100% 100%;
            background-repeat: no-repeat;
            vertical-align: top;
             /* &  */
            &.decrease {
              background-image: url('decrease_2@2x.png');
            }
            &.discount {
              background-image: url('discount_2@2x.png');
            }
            &.invoice {
              background-image: url('invoice_2@2x.png');
            }
            &.special {
              background-image: url('special_2@2x.png');
            }
            &.guarantee {
              background-image: url('guarantee_2@2x.png');
            }
          }
          .text {
            font-size: 10px;
            margin-left: 4px;
          }
        }
      }
    }
    /* .header  2 */
		.supportsbtn {
			position: absolute;   /*  */
			right: 12px;
			bottom: 40px;
			background: rgba(0, 0, 0, .2);
			height: 24px;
			line-height: 24px;
			font-size: 0;
			border-radius: 12px;
			padding: 0 8px;
			.count {
				font-size: 10px;
				margin-right: 2px;
			}
			.icon {
				font-size: 12px;
				line-height: 24px;
				vertical-align: top;
			}
		}
    /* .header  3 */
    .bulletin-wrapper {
      height: 28px;
      position: relative;
      background: rgba(7, 17, 27, .2);
      .title {
        position: absolute;
        top: 8px;
        left: 12px;
        display: inline-block;
        width: 22px;
        height: 12px;
        background-image: url('bulletin@2x.png');
        background-size: 100% 100%;
        background-repeat: no-repeat;
      }
      .text {
        display: block;
        padding: 0 30px 0 38px;
        line-height: 28px;
        font-size: 10px;
      }
      .out_text_hidden {
        white-space: nowrap;   /* :  */
        overflow: hidden;   /* :  */
        text-overflow: ellipsis;   /* : */
      }
      .icon {
        position: absolute;
        top: 0;
        right: 12px;
        line-height: 28px;
        font-size: 16px;
      }
    }
    /* .header  4 */
    .bg_img {
      position: absolute;
      top: 0;
      left: 0;
      /*z-index  position  relative, absolute  fixed .*/
      z-index: -1;   /*  */
			width: 100%;
			height: 100%;
			filter: blur(5px);   /* (<img>) |  */
			-webkit-filter: blur(5px);   /*  chrome & safari */

    }
		/* .header  5 */
		.detail {
			position: fixed;   /*  */
			top: 0;
			left: 0;
			z-index: 10;   /*  */
			width: 100%;
			height: 100%;
			display: flex;   /*  */
			flex-direction: column;   /*  | */
			background: rgba(7, 17, 27, .8);
			overflow: auto;   /*  */
			transition: all .5s;   /*  opacity,  1s*/
			&.fade-enter-active, &.fade-leave {   /*  &  */
				opacity: 1;
				background: rgba(7, 17, 27, .8);
			}
			&.fade-enter, &.fade-leave-active {   /*  &  */
				opacity: 0;
				background: rgba(7, 17, 27, 0);
			}
			/*. detail  1 */
			.content {
				flex: 1;   /*  */
				text-align: center;
				.name {
					font-size: 16px;
					font-weight: 700;   /*  */
					color: #009688;
					text-align: center;
					margin: 64px 0 16px 0;
				}
				.title {
					display: flex;
					width: 80%;
					margin: 28px auto 24px auto;
					.line {
						flex: 1;
						margin-bottom: 6px;   /*  */
						border-bottom: 1px solid rgba(255, 255, 255, .2);  /*  */
					}
					.text {
						padding: 0 12px;   /*  */
						font-size: 14px;
						color: #009688;
						font-weight: 600;
					}
				}
				.supports {
					text-align: left;
					font-size: 12px;
					color: #009688;
					font-weight: bold;
					width: 80%;
					margin: 0 auto;   /*  */
					padding: 0 12px;
					.support-item {
						margin-bottom: 12px;
						padding: 0 12px;
						.text {
							font-size: 12px;
							margin-left: 6px;
							line-height: 16px;
						}
						/*  */
						&:last-child {
							margin-bottom: 0;
						}
					}
				}
				.bulletin {
					text-align: left;   /*  */
					width: 80%;
					margin: 0 auto;
					> p {
						font-size: 12px;
						color: #009688;
						line-height: 2;   /*  |   */
						padding: 0 12px;
					}
				}
			}
			/* .detail  2 */
			.close {
				text-align: center;
				margin: 32px 0;   /*  */
				.icon {
					font-size: 32px;
					color: #009688;
				}
			}
		}
	}
</style>



