<template>
	<div class="star_wrapper" :class="star_type">
		<span class="star_item" v-for="item of items" :class="item"></span>
	</div>
</template>

<script>
//  | 
const STAR_LENGTH = 5;

export default {
	//  
	props: {
		//  Number 
		size: Number,
		score: Number
	},
	// 
	computed: {
		// 
		star_type() {
			return 'star-' + this.size;   //  size
		},
		items() {
			// let   let 
			let result = [];
			// let score = Math.floor(this.score * 2) / 2;
			//  
			let score = this.score - 2;
			//  | Boolean 
			let has_decimal = score % 1 !== 0;
			//  |  floor
			let star_integer = Math.floor(score);
			for (let i = 0; i < star_integer; i++) {
				// push() 
				result.push('on');
			}
			if (has_decimal) {
				result.push('half');
			}
			while (result.length < STAR_LENGTH) {
				result.push('off');
			}
			// 
			return result;
		}
	}
};
</script>

<style type="text/css" lang="less" scoped>
.star_wrapper {
	display: inline-block;
	text-align: center;
}

.star-20 {
	.star_item {
		display: inline-block;   /*  |  */
		width: 20px;
		height: 20px;
		margin-right: 12px;
		background-size: 100% 100%;   /*  */
		background-repeat: no-repeat;
	}
	.on {
		background-image: url('./star48_on@3x.png');
	}
	.half {
		background-image: url('./star48_half@3x.png');
	}
	.off {
		background-image: url('./star48_off@3x.png');
	}
}

.star-15 {
	.star_item {
		display: inline-block;
		width: 15px;
		height: 15px;
		margin-rigth: 6px;
		background-size: 100% 100%;
		background-repeat: no-repeat;
	}
	.on {
		background-image: url('./star48_on@3x.png');
	}
	.half {
		background-image: url('./star48_half@3x.png');
	}
	.off {
		background-image: url('./star48_off@3x.png');
	}
}

.star-12 {
	.star_item {
		display: inline-block;
		width: 12px;
		height: 12px;
		margin-right: 3px;
		background-size: 100% 100%;
		background-repeat: no-repeat;
	}
	.on {
		background-image: url('./star48_on@3x.png');
	}
	.half {
		background-image: url('./star48_half@3x.png');
	}
	.off {
		background-image: url('./star48_off@3x.png');
	}
}
</style>



